<template>
  <div style="height:100%;">
    <header>
      <h1>
        <img src="../assets/jy_logo_white.png" class="logo">
        <span @click="goSchoolCard" style="cursor: pointer;">校园社群管理平台</span>
      </h1>
      <div class="school_name">
        <span style="border-left:2px solid hsla(0,0%,100%,.2);margin-right:10px;"></span>
        <span>{{userInfo.schoolName}}</span>
      </div>
      <div class="header_right">
        <img class="headicon" :src="tools.cdn(userInfo.userIcon, 'head_icon.png', 128)">
        <span class="username">{{userInfo.userName}}</span>
        <div class="lagout_button" @click='logout'>退出</div>
      </div>
    </header>
    <div class="nav">
        <div class="nav_wrap">
          <el-menu class="el-menu-vertical-demo" background-color="#19202f" text-color="#fff" active-text-color="#67c23a" unique-opened>
            <el-submenu :index="index.toString()" v-for="(item, index) in navList" :key="index">
              <template slot="title">
                <i class="iconfont change_color" v-bind:class="item.icon"></i>
                <span>{{item.title}}</span>
              </template>
              <el-menu-item-group>
                <el-menu-item :index="index.toString() + '-' + tag.toString()" v-for="(nav, tag) in item.paths" :key="tag" @click="navFun(nav, tag)">{{nav.name}}</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </div>
    </div>
    <router-view style="width:calc(100% - 200px);height:calc(100% - 60px);background-color: #f1f3f6;float:left;"/>
    <textarea id="textAreaXXDD" style="opacity: 0;position: absolute;top: 0; left: -99999px;"></textarea>
  </div>
</template>

<script>
import api from './api.js'
export default {
  data () {
    return {
      type: false,
      userName: 'widem',
      userImg: '',
      featureList: [],
      navList: []
    }
  },
  mounted () {
    this.$store.dispatch('getInfo', this.$route.params.schoolId) // beforeCreate ()
    api.getTeacherFeatures({schoolId: this.$route.params.schoolId}, (data) => {
      this.initNav(data)
    }, (er) => {})
  },
  computed: {
    userInfo () {
      return this.$store.state.userInfo
    }
  },
  methods: {
    initNav (data) {
      data.forEach(element => {
        if (element.featureCode === 'LAOSHIGUANLI') {
          this.navList.push(
            {
              title: '老师管理',
              featureCode: 'LAOSHIGUANLI',
              paths: [{path: 'TeacherImport', name: '导入老师'}],
              icon: 'icon-laoshi'
            }
          )
        } else if (element.featureCode === 'XUESHENGXINXI') {
          this.navList.push(
            {
              title: '我的班级',
              featureCode: 'XUESHENGXINXI',
              paths: [{path: 'StudentManageMy', name: '学生管理'}, {path: 'StudentImport', name: '导入学生'}],
              icon: 'icon-xuesheng'
            }
          )
          this.$store.commit('isClassMaster', true)
        } else if (element.featureCode === 'BANJIGUANLI') {
          if (parseInt(this.$store.state.userInfo.schoolModel) === 2) {
            this.navList.push(
              {
                title: '班级学生',
                featureCode: 'XUESHENGBANJIGUANLIXINXI',
                paths: [{path: 'StudentManage', name: '学生管理'}, {path: 'ImportStudent', name: '导入学生'}],
                icon: 'icon-banji'
              }
            )
          } else {
            this.navList.push(
              {
                title: '班级学生',
                featureCode: 'XUESHENGBANJIGUANLIXINXI',
                paths: [{path: 'GradeManage', name: '年级管理'}, {path: 'ClassManage', name: '班级管理'}, {path: 'StudentManage', name: '学生管理'}, {path: 'ImportStudent', name: '导入学生'}],
                icon: 'icon-banji'
              }
            )
          }
          this.$store.commit('isManager', true)
        } else if (element.featureCode === 'CHENGJI') {
          this.navList.push(
            {
              title: '成绩管理',
              featureCode: 'CHENGJI',
              paths: [{path: 'AchievementImport', name: '导入成绩'}, {path: 'AchievementList', name: '查看成绩'}],
              icon: 'icon-chengji'
            }
          )
        } else if (element.featureCode === 'ZUOYE') {
          this.navList.push(
            {
              title: '作业管理',
              featureCode: 'ZUOYE',
              paths: [{path: 'PublishHomework', name: '发布作业'}, {path: 'HomeworkList', name: '作业列表'}],
              icon: 'icon-zuoye'
            }
          )
        } else if (element.featureCode === 'GONGGAOGUANLI') {
          this.navList.push(
            {
              title: '公告管理',
              featureCode: 'GONGGAOGUANLI',
              paths: [{path: 'NoticePublish', name: '发布公告'}, {path: 'NoticeList', name: '公告列表'}],
              icon: 'icon-qungonggaoguanli'
            }
          )
        } else if (element.featureCode === 'DEYUGUANLI') {
          this.navList.push(
            {
              title: '德育管理',
              featureCode: 'DEYUGUANLI',
              paths: [{path: 'StudentEvaluationSetting', name: '学生操行评比项设置'}, {path: 'ClassEvaluationSetting', name: '星级班级评比项设置'}],
              icon: 'icon-deyuguanli'
            }
          )
        }
      })
    },
    navFun (code, index) {
      this.$router.push({'name': code.path})
    },
    goSchoolCard () {
      this.$router.push({name: 'SchoolCard'})
    },
    logout () {
      this.$confirm('是否退出登录?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        api.homeLogout((data) => {
          window.location.reload()
        }, (er) => {
          if (er.toUser !== undefined && er.toUser) {
            this.$message.error(er.message)
          } else {
            this.$message.error('退出失败')
          }
        })
      }).catch(() => {})
    }
  }
}
</script>

<style>
[v-cloak] { display: none }
header {
  width: 100%;
  height: 60px;
	background: #1e3d80;
}
header .logo {
  display: inline-block;
  width: 50px;
  height: 50px;
  margin: 5px 10px 0 15px;
  float: left;
}
header h1{
  width: 250px;
  height: 60px;
  /* background: #a2d268; */
  line-height: 60px;
  font-size: 20px;
  float: left;
  color: #fff;
  text-align: center;
}
header .school_name{
  float: left;
  height: 60px;
  line-height: 60px;
  color: #fff;
  font-size: 20px;
  font-weight: 500;
}
header .header_right{
  float: right;
}
header .headicon{
  width: 40px;
	height: 40px;
	display: inline-block;
  border-radius: 50%;
  border: none;
  float: left;
  margin: 10px 20px 0 0;
}
header .header_right .username{
  float: left;
  line-height: 60px;
  color: #fff;
  margin-right: 20px;
}
header .header_right .lagout_button{
  width: 70px;
  text-align: center;
  height: 30px;
  line-height: 30px;
  font-size: 16px;
  margin:15px 40px 0 0;
  color:#fff;
  cursor: pointer;
  border-left: 2px solid hsla(0,0%,100%,.2);
  float: left;
}
.nav {
  width: 200px;
  padding-top: 20px;
  box-sizing: border-box;
	height: calc(100% - 60px);
  background: #19202f;
	float: left;
}
.el-menu{
  border-right:none !important;
}
.el-menu-item-group__title{
	display: none;
}
.change_color{
	color: #fff !important;
}
</style>



