import Vue from 'vue'
import Router from 'vue-router'
import SchoolCard from '@/page/SchoolCard'
import Home from '@/page/Home'
import Login from '@/page/Login'
import StudentRouter from '@/page/student/router'
import TeacherRouter from '@/page/teacher/router'
import AchievementRouter from '@/page/achievement/router'
import NoticeRouter from '@/page/notice/router'
import HomeworkRouter from '@/page/homework/router'
import ClassStudentRouter from '@/page/grademanage/router'
import PublicRouter from '@/page/public/router'
import MoralRouter from '@/page/moral/router'

Vue.use(Router)
const appChildRouters = Array.of(
  ...StudentRouter,
  ...TeacherRouter,
  ...AchievementRouter,
  ...HomeworkRouter,
  ...NoticeRouter,
  ...ClassStudentRouter,
  ...PublicRouter,
  ...MoralRouter
)
const router = new Router({
  hashbang: true,
  history: true,
  mode: 'hash',
  routes: [
    {
      path: '/', redirect: { name: 'SchoolCard' }
    },
    {
      path: '/edu/:schoolId',
      children: appChildRouters,
      component: Home
    },
    {
      path: '/school_card',
      name: 'SchoolCard',
      component: SchoolCard
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    }
  ]
})

export default router



