import FormateTime from './FormateTime'
export const tools = {
  cdn (path, def, size) {
    if (path) {
      if (path.startsWith('data:')) {
        return path
      } else {
        if (size === undefined) {
          return process.env.CDN_DOMAIN + '/' + path
        } else {
          return process.env.CDN_DOMAIN + '/' + path + '?imageView2/1/w/' + size + '/h/' + size
        }
      }
    } else {
      if (def) {
        return require('@/assets/' + def)
      } else {
        return ''
      }
    }
  },
  isNull (data) {
    return data === '' || data === undefined || data === null
  },
  data2blob (data) {
    // dataURL 的格式为 “data:image/png;base64,****”,逗号之前都是一些说明性的文字，我们只需要逗号之后的就行了
    if (data) {
      let mime
      if (data.indexOf('data:') === 0) {
        mime = data.split(',')[0].replace('data:', '').replace(';base64', '')
        data = data.split(',')[1]
      } else {
        mime = 'image/jpg'
      }
      data = window.atob(data)
      var ia = new Uint8Array(data.length)
      for (var i = 0; i < data.length; i++) {
        ia[i] = data.charCodeAt(i)
      };
        // canvas.toDataURL 返回的默认格式就是 image/png
      return new Blob([ia], {
        type: mime
      })
    }
  },
  formateTime (date, fmt = 'YYYY-MM-DD HH:mm:ss') {
    return FormateTime(date, fmt)
  }
}
export default {
  install: function (Vue) {
    Vue.prototype.tools = tools
  }
}



