<template>
  <div style="height: 100%;" v-if="schoolData.length > 0">
      <header>
        <img src="../assets/jy_logo_white.png">
        <h1>校园社群管理平台</h1>
        <div class="header_right">
          <div class="lagout_button" @click="logout">退出</div>
        </div>
      </header>
      <div class="content">
        <el-row :gutter="10">
          <el-col :span="6" v-for="(item, index) in schoolData" :key="index" v-if="item.schoolStatus != '2'">
            <div class="card_info">
              <img :src="tools.cdn(item.schoolLogo, 'jy_logo.png', 128)">
              <p class="school_name">
                <span>学校名称：{{item.schoolName}}</span><br>
                <span v-if="item.schoolModel !== null && item.schoolModel !== undefined">{{item.schoolModel == 2 ? '（个人版）' : '（机构版）'}}</span>
              </p>
              <span class="button_come" @click="getPower(item)">点击进入</span>
            </div>
          </el-col>
        </el-row>
      </div>
  </div>
</template>
<script>
import api from './api.js'
export default {
  data () {
    return {
      schoolData: []
    }
  },
  beforeCreate () {
    this.$store.dispatch('getSchoolList')
  },
  computed: {
    schoolList () {
      return this.$store.state.schoolList
    }
  },
  watch: {
    schoolList: function (newSchoolList, old) {
      if (newSchoolList.length > 0) {
        this.schoolData = newSchoolList
      } else {
        this.$alert('对不起，请您在手机微信中打开！', '温馨提示', {
          confirmButtonText: '确定',
          callback: action => {
            api.homeLogout((data) => {
              window.location.reload()
            }, (er) => {})
          }
        })
      }
    }
  },
  methods: {
    getPower (item) {
      if (item.memberType.indexOf('TEACHER') !== -1 || item.memberType.indexOf('MASTER') !== -1) {
        this.$router.push({name: 'Welcome', params: {schoolId: item.schoolId}})
      } else {
        this.$alert('对不起，请您在手机微信中打开！', '温馨提示', {
          confirmButtonText: '确定',
          callback: action => {}
        })
      }
    },
    logout () {
      this.$confirm('是否退出登录?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        api.homeLogout((data) => {
          window.location.reload()
        }, (er) => {
          if (er.toUser !== undefined && er.toUser) {
            this.$message.error(er.message)
          } else {
            this.$message.error('退出失败')
          }
        })
      }).catch(() => {})
    }
  }
}
</script>
<style scoped>
header {
  width: 100%;
  height: 60px;
	background: #1e3d80;
}
header img {
  display: inline-block;
  width: 50px;
  height: 50px;
  margin-top: 5px;
  margin-left: 15px;
  float: left;
}
header h1{
  width: 200px;
  height: 60px;
  line-height: 60px;
  font-size: 20px;
  float: left;
  color: #fff;
  text-align: center;
}
header .header_right{
  float: right;
}
header .header_right .lagout_button{
  width: 70px;
  text-align: center;
  height: 30px;
  line-height: 30px;
  font-size: 16px;
  margin:15px 40px 0 0;
  color:#fff;
  cursor: pointer;
  border-left: 2px solid hsla(0,0%,100%,.2);
}
.content{
  padding: 0 20px 10px 20px;
  box-sizing: border-box;
  width: 100%;
  height: calc(100% - 60px);
  /* height: 100%; */
  overflow-y: auto;
  background: rgb(241, 243, 246);
}
.content .card_info{
  width: 100%;
  height: 300px;
  background: #fff;
  margin-top:10px;
  text-align: center;
}
.content .card_info .school_name{
  width: 230px;
  margin: 15px auto;
  overflow:hidden;
  text-overflow:ellipsis;
  white-space:nowrap 
}
.content .card_info .button_come{
  display: inline-block;
    width: 100px;
    height: 36px;
    text-align: center;
    background: #67c23a;
    line-height: 36px;
    color: #fff;
    border-radius: 5px;
    cursor: pointer;
    margin-left: 15px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
.content .card_info .button_come:hover{
  background: #85ce61;
  border-color: #85ce61;
  color: #fff;
}
.content .card_info .button_come:active{
  background: #5daf34;
  border-color: #5daf34;
  color: #fff;
}
.content .card_info img {
	width: 120px;
	height: 120px;
	display: inline-block;
	margin-top: 30px;
	margin-bottom: 10px;
	border: none;
}
</style>




